import React from 'react';
import slide from './Slide.module.css';


export const Slide = props => {	
	return 	<div className = {slide.container} onClick = {() => props.showSlide(null)}>
					<div className = {slide.editBlock} onClick = {(event) => event.stopPropagation()}>
						<div className = {slide.slider} style = {{backgroundImage:`url(${process.env.PUBLIC_URL + props.allPhotos.filter( item => props.isShowSlide === item )})`}}>
						</div>					
				</div>	
			</div>
}




