import { connect } from 'react-redux';
import { Photos } from './Photos';
import { viewPhotos, nextSlide, prevSlide, showSlide } from '../../store/PhotosReducer';
import { getPhotos, getAllPhotos, getSlide, getUnderSlidePhotos, authUserId, isShowSlide } from '../../store/selectors/selectors';
import { compose } from 'redux';
import { withoutAuthRedirect } from '../../hoc/withAuthRedirect';


const mapStateToProps = (state) => ({
	authUserID: authUserId(state),
	view: state.PhotosReducer.view,
	source: state.PhotosReducer.photosArray,
	photos: getPhotos(state),	
	numberSlidePhoto: state.PhotosReducer.numberSlidePhoto,
	allPhotos: getAllPhotos(state),
	slide: getSlide(state),
	underSlidePhotos: getUnderSlidePhotos(state),
	isShowSlide: isShowSlide(state)
})

export const PhotosContainer = compose(connect(mapStateToProps, {viewPhotos, nextSlide, prevSlide, showSlide}), withoutAuthRedirect)(Photos)